import { render } from "@testing-library/react";
import React,{Component} from "react";
import { ButtonDropdown, DropdownItem, DropdownMenu, DropdownToggle } from "reactstrap";

class R037_ReactstrapDropdown extends Component{

    constructor (props){
        super(props);
        this.state = {dropdownOpen:false}
    }
    toggle = (e) =>{this.setState({dropdownOpen:!this.state.dropdownOpen})};

render(){

return(
    <div style={{marginBottom:'300px'}}>
        <ButtonDropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}> 
            <DropdownToggle caret>  Dropdown</DropdownToggle>
                <DropdownMenu> 
                    <DropdownItem header></DropdownItem>
                    <DropdownItem disabled></DropdownItem>
                    <a href="http:example.com/"></a>
                    <DropdownItem >example  .</DropdownItem>
                    <DropdownItem onClick={(e)=>alert('Alert ')}></DropdownItem>
               </DropdownMenu>
        </ButtonDropdown>
    </div>
)

}



}




export default R037_ReactstrapDropdown;